<template>
  <form @submit.prevent="validate">
    <div class="mb-3">
      <label for="exampleInputEmail1" class="form-label">title</label>
      <input type="text" class="form-control" v-model.lazy="form.title" />
      <div id="emailHelp" class="form-text text-danger">
        {{ form.textErrorTitle }}
      </div>
    </div>
    <div class="mb-3">
      <label for="exampleInputPassword1" class="form-label">body</label>
      <textarea class="form-control" rows="6" v-model.lazy="form.body" />
      <div id="emailHelp" class="form-text text-danger">
        {{ form.textErrorBody }}
      </div>
    </div>
    <button type="submit" class="btn btn-dark" :disabled="loadingButton">
      <div
        v-if="loadingButton"
        class="spinner-border spinner-border-sm"
        role="status"
      ></div>
      {{ nameButton }}
    </button>
  </form>
</template>

<script>
import { reactive } from "@vue/reactivity";
export default {
  props: {
    nameButton: String,
    loadingButton: Boolean,
    post: Object,
  },
  setup(props, { emit }) {
    console.log(props.nameButton);
    const form = reactive({
      title: "",
      textErrorTitle: "",
      body: "",
      textErrorBody: "",
    });

    function setInput() {
      if (props.post !== undefined) {
        form.title = props.post.title;
        form.body = props.post.body;
      }
    }
    setInput();

    function validate() {
      if (form.title === "") {
        form.textErrorTitle = "title is required";
      } else {
        form.textErrorTitle = "";
      }
      if (form.body === "") {
        form.textErrorBody = "body is required";
      } else {
        form.textErrorBody = "";
      }
      if (form.title !== "" && form.body !== "") {
        emit("formData", form);
      }
    }
    return { form, validate };
  },
};
</script>

