<template>
  <div class="container">
    <div class="row">
      <div class="col-md-6">
        <div v-if="pageLoading" class="spinner-border" role="status">
        <span class="visually-hidden">Loading...</span>
      </div>
        <h2 v-else class="mb-5">EditPost :</h2>
        <PostForm
          @formData="updatePost"
          name-button="edit"
          :loading-button="loading"
          :post="post"
        />
      </div>
    </div>
  </div>
</template>

<script>
import PostForm from "../../components/post/Form.vue";
import { ref } from "@vue/reactivity";
import Swal from "sweetalert2";
import axios from "axios";
import { useRoute } from "vue-router";

export default {
  components: {
    PostForm,
  },
  setup() {
   const post = ref({})
    const loading = ref(false);
    const pageLoading = ref(true)
    const route = useRoute();
    function getPost() {
      axios
        .get(`https://jsonplaceholder.typicode.com/posts/${route.params.id}`)
        .then((response) => {
        post.value = response.data
          pageLoading.value = false
        })
        .catch(() => {
          console.log("lkjh");
        });
    }
    getPost();

  
    function updatePost(formData) {
      loading.value = true;
      axios
        .put(`https://jsonplaceholder.typicode.com/posts/${route.params.id}`, {
          id: route.params.id,
          title: formData.title,
          body: formData.body,
          userId: 1,
        })
        .then(() => {
          loading.value = false;
          Swal.fire({
            title: "thanks!",
            text: " post update successfuly",
            icon: "success",
            confirmButtonText: "ok",
          });
        })
        .catch((err) => {
          console.log(err);
        });
    }
    return {loading, updatePost,post,pageLoading };
  },
};
</script>

