<template>
  <div class="container">
    <div class="row">
      <div class="col-md-6">
        <h2 class="mb-5">CreatePost :</h2>
         <PostForm @formData = "createPost" name-button="craete" :loading-button="loading"/>
      </div>
    </div>
  </div>
</template>

<script>
import PostForm from "../../components/post/Form.vue"
import {ref } from "@vue/reactivity";
import Swal from "sweetalert2";
import axios from "axios";
export default {
  components:{
    PostForm
  },
  setup() {
    const loading = ref(false);


    function createPost(formData) {
      loading.value = true;
      console.log(formData);
      axios
        .post("https://jsonplaceholder.typicode.com/posts", {
          title: formData.title,
          body: formData.body,
          userId: 1,
        })
        .then(() => {
          loading.value = false;
          Swal.fire({
            title: "thanks!",
            text: "create post successfuly",
            icon: "success",
            confirmButtonText: "ok",
          });
        })
        .catch((err) => {
          console.log(err);
        });
    }
    return {createPost, loading };
  },
};
</script>

