import { createRouter, createWebHistory } from "vue-router";
import Home from "./pages/Home.vue";
import Users from "./pages/users/index.vue";
import Template from "./pages/users/Template.vue";
import ShowUser from "./pages/users/Show.vue";
import Posts from "./pages/post/Posts.vue"
import TemplatePost from "./pages/post/TemplatePost.vue"
import ShowPost from "./pages/post/ShowPost.vue"
import CreatePost from "./pages/post/CreatePost.vue"
import EditPost from "./pages/post/EditPost.vue"

const routes = [
  { path: "/", name: "home", component: Home },
  {
    path: "/users",
    name: "userTemplate",
    component: Template,
    children: [
      { path: "", name: "users", component: Users },
      { path: ":id", name: "userId", component: ShowUser },
    ],
  },
  {
    path: "/posts",
    name: "postTemplate",
    component: TemplatePost,
    children: [
      { path: "", name:"posts", component: Posts },
      { path: ":id", name: "postId", component: ShowPost },
      { path: "create", name: "createPost", component: CreatePost },
      { path: "edit/:id", name: "editPost", component: EditPost },
    ],
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
